.card-container {
  border-radius: 10px;
  width: 370px;
  height: 414px;
  transition: 0.4s;
  box-shadow: 7px 0px 30px 0px rgba(0, 0, 0, 0.2);

  cursor: pointer;
}

.card-container .image {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px 5px 0 0;
  width: 370px;
  height: 200px;
  overflow: hidden;
  position: relative;
}
.card-container .image p {
  font: 800 18px "Mulish", sans-serif;
  color: white;
  width: 290px;
  height: 53px;
  position: absolute;
  bottom: 0;
  margin-bottom: 20px;
  z-index: 2;
}

.card-container .image .overlay {
  position: absolute;
  top: -50px;
  left: 0;
  right: 0;
  z-index: 1;
  width: 100%;
  height: 170%;
  transition: 0.4s;
  background: linear-gradient(to top, rgba(0, 0, 0, 1) 10%, rgba(0, 0, 0, 0));
}

.card-container .text {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  height: 211px;
  padding: 0 40px;
}

.card-container .text p {
  font: 400 18px "Mulish", sans-serif;
}

.card-container .text .line {
  position: relative;
  width: 100%;
  height: 1px;
  margin-top: 40px;
  background: #e0e0e0;
}

.card-container .text .line-yellow {
  position: absolute;
  height: 1px;
  width: 0%;
  left: 0;
  background: #a7bd25;
  transition: width 0.3s;
}

.card-container .text h1 {
  margin-top: 19px;
  color: #bcbcbc;
  letter-spacing: 2px;
  font: 800 14px "Mulish", sans-serif;
}

.card-container:hover .image .overlay {
  transform: translate(0, -50px);
}

.card-container:hover .image p {
  color: #a7bd25;
}

.card-container:hover .text h1 {
  color: #a7bd25;
}

.card-container:hover .text .line-yellow {
  width: 100%;
}

.card-container:hover {
  box-shadow: 7px 0px 30px 0px rgba(0, 0, 0, 0.3);
}
